<template>
    <div>
        <div v-if="api.length <= 0">
            <v-file-input 
                v-model="file" 
                label="Submit you api zip file." 
                accept="zip,application/octet-stream,application/zip,application/x-zip,application/x-zip-compressed"
                show-size
            />
            
            <v-btn @click="submitFile()">Submit</v-btn>
        </div>

        <div v-else>
            This is your new api <strong><a :href="api">{{ api }}</a></strong>.
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'CreateApi',

    data: () => ({
        file: [],
        api: ''
    }),

    methods: {
        async submitFile(){
            let formData = new FormData()

            formData.append('file', this.file)

            let res = await axios.post('http://localhost:3000/createApi',
                formData,
                {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }
            )

            this.api = res.data
        }
    }
}
</script>

