<template>
  <div>
    <v-dialog 
      v-model="newPasswordDialog"
    >
      <v-form 
        ref="newPasswordform"
        v-model="newPasswordValid"
      >
        <v-card>
          <v-card-title 
            class="display-1 primary--text"
          >
            New Password
          </v-card-title>

          <v-card-text>
            <v-text-field
              v-model="newPassword"
              label="Password"
              type="password"
              :rules="passwordRules"
            ></v-text-field>

            <v-text-field
              v-model="confirmPassword"
              label="Confirm Password"
              type="password"
              :rules="confirmPasswordRules.concat(passwordConfirmationRule)"
            ></v-text-field>
          </v-card-text>

          <v-card-actions>
              <v-btn
                color="primary"
                text
                @click="closeNewPasswordDialog"
              >
                confirm
              </v-btn>
            </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>

    <v-form 
      ref="form"
      v-model="valid"
    >
      <v-container classs="d-flex justify-center">
        <div 
          class="display-1 primary--text"
        >
          UVUCS Login
        </div>
        
        <v-text-field
          v-model="username"
          label="Username"
          :rules="usernameRules"
        ></v-text-field>

        <v-text-field
          v-model="password"
          label="Password"
          type="password"
          :rules="passwordRules"
        ></v-text-field>
        
        <v-btn 
          class="mr-2" 
          color="primary"
          text
          :loading="loading"
          @click="signIn"
        >
          Login
        </v-btn>
      </v-container>
    </v-form>
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from 'vuex'

  export default {
    name: 'Login',

    data: () => ({
      newPasswordDialog: false,
      newPasswordValid: false,
      valid: false,
      loading: false,

      newPassword: '',

      confirmPassword: '',
      confirmPasswordRules: [
        v => !!v || 'Cownfirm Password is required'
      ],

      username: '',
      usernameRules: [
        v => !!v || 'Username is required'
      ],
      password: '',
      passwordRules: [
        v => !!v || 'Password is required'
      ]
    }),
    
    computed: {
      ...mapState(['user']),

      passwordConfirmationRule() {
        return () =>
          this.newPassword === this.confirmPassword || 'Passwords must match'
      }
    },

    methods: {
      ...mapMutations(['setIsLoggedIn']),

      ...mapActions(['login', 'completeNewPassword']),

      validate () {
        this.$refs.form.validate()
      },

      closeNewPasswordDialog() {
        this.$refs.newPasswordform.validate()

        if (this.newPasswordValid) {
          if (this.completeNewPassword({password: this.newPassword})) {
            this.newPasswordDialog = false
          }
        }
      },

      async signIn() {
        this.loading = true
        this.validate()

        if (this.valid) {
          const user = await this.login({username: this.username, password: this.password})
          
          if (user) {
            if (user.challengeName === 'NEW_PASSWORD_REQUIRED') {
              this.newPasswordDialog = true
            } else {
              this.setIsLoggedIn({ isLoggedIn: true })
            }
          } else {
            this.$refs.form.reset()
            //TODO: error bad login
          }
        }

        this.loading = false
      }
    }
  }
</script>
