import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import CreateApi from '@/views/CreateApi.vue'
import CreateWebsite from '@/views/CreateWebsite.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/create-api',
    name: 'CreateApi',
    component: CreateApi
  },
  {
    path: '/create-website',
    name: 'CreateWebsite',
    component: CreateWebsite
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
