import Vue from 'vue'
import Vuex from 'vuex'

import { Auth } from 'aws-amplify'
import { createWebsite } from '../service/CreateWebsiteService.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {},
    isLoggedIn: false,
   //apiUrl: 'http://192.168.0.22:8080'
    apiUrl: 'http://uvubackendwebapp-env.eba-hipsumk4.us-west-2.elasticbeanstalk.com'
  },

  mutations: {
    setUserData(state, { user }) {
      state.user = user
    },

    setIsLoggedIn(state, { isLoggedIn }) {
      state.isLoggedIn = isLoggedIn
    }
  },

  actions: {
    async onLoad({ commit }) {
      try {
        let user = await Auth.currentSession()

        commit('setUserData', { user })
        commit('setIsLoggedIn', { isLoggedIn: true })
      }
      catch(error) {
        console.log(error)
      }
    },

    async logout({ commit }) {
      try {
          await Auth.signOut()

          commit('setUserData', { user: {}})
          commit('setIsLoggedIn', { isLoggedIn: false })

          return true
      } catch (error) {
          commit('setIsLoggedIn', { isLoggedIn: true })

          return false
      }
    },

    async login({ commit }, {username, password}) {
      try {
        let user = await Auth.signIn(username, password)

        commit('setUserData', { user: user.signInUserSession })
        commit('setIsLoggedIn', { isLoggedIn: true })

        return user
      } catch (error) {
        console.log(error)
        commit('setIsLoggedIn', { isLoggedIn: false })
        return false
      }
      
    },

    completeNewPassword({ commit, state }, { password }) {
      try {
        Auth.completeNewPassword(state.user, password)
        
        commit('setIsLoggedIn', { isLoggedIn: true })

        return true
      }  catch (error) {
        commit('setIsLoggedIn', { isLoggedIn: false })
        return false
      }
    },

    createWebsite({ state }, { formData, username }) {
      console.log(state)
      try {
        let data = createWebsite({ formData, username })

        return data
      } catch (error) {
        console.log(error)
      }
    }
  }
})
