<template>
    <div>
        <div class="d-flex flex-row justify-left pl-3">
            <h1 class="primary--text font-weight-light">
                Create Website
            </h1>
        </div>

        <div v-if="website.length <= 0">
            <div class="d-flex flex-row justify-right pt-3 pl-3">
                Submit a zip file with an index.html and any other content for your website.
            </div>

            <v-file-input 
                v-model="file" 
                class="d-flex flex-row justify-right mx-3"
                label="Submit website file." 
                accept="application/zip,application/x-zip,application/x-zip-compressed,application/octet-stream"
                show-size
            />
            
            <v-btn
                class="d-flex flex-row justify-right ml-3"
                @click="submitFile()"
            >
                Submit
            </v-btn>
        </div>

        <div v-else>
            <div class="d-flex flex-row justify-right pt-3 pl-3">
                <a :href="website" target="blank"> Go to your new website. </a>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'

export default {
    name: 'CreateWebsite',

    data: () => ({
        file: [],
        website: ''
    }),

    computed: {
        ...mapState(['user'])
    },

    methods: {
        ...mapActions(['createWebsite']),

        async submitFile(){
            let formData = new FormData()

            formData.append('file', this.file)
            formData.append('username', this.user.idToken.payload['cognito:username'])

            let res = await this.createWebsite({ formData }) 
            this.website = res.data
        }
    }
}
</script>

